.App {
  position: relative;
  width: 100%;
  min-height: 100vh;
  background: #f2f2f7; }
  .App.Authenticated:before {
    content: "";
    position: absolute;
    background: #6079f2;
    width: 200%;
    left: -50%;
    top: -50vh;
    height: 100vh;
    border-radius: 150px;
    -webkit-transform: rotate(6.5deg);
    -ms-transform: rotate(6.5deg);
    transform: rotate(6.5deg); }

.App_container {
  position: relative;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  min-width: 1200px;
  max-width: 1400px;
  padding-left: 320px; }

.App_wrapper {
  position: relative;
  padding: 75px 50px;
  width: 100%; }

/* Hack - will fix later */
.switch {
  border: 1px solid #ccc;
  width: 50px;
  height: 26px;
  border-radius: 13px;
  cursor: pointer;
  display: inline-block; }

.switch-toggle {
  border: 1px solid #6079f2;
  -webkit-box-shadow: 1px 1px 1px #ccc;
  box-shadow: 1px 1px 1px #ccc;
  width: 25px;
  height: 24px;
  left: 0;
  border-radius: 12px;
  background: #fff;
  position: relative;
  -webkit-transition: left 0.2s ease-in-out;
  -o-transition: left 0.2s ease-in-out;
  transition: left 0.2s ease-in-out; }

.switch.on {
  background: #6079f2; }

.switch.on .switch-toggle {
  left: 23px; }

.switch.disabled {
  cursor: not-allowed; }



