.Modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;
  overflow-y: auto;
  background-color: rgba(242, 242, 247, 0.7); }

.Modal_content {
  margin: 50px auto;
  position: relative;
  width: 100%;
  max-width: 600px;
  padding: 50px;
  border-radius: 10px;
  background-color: white;
  -webkit-box-shadow: 0 5px 15px rgba(90, 88, 94, 0.3);
  box-shadow: 0 5px 15px rgba(90, 88, 94, 0.3); }
  .Modal_content .Button-submit {
    margin-top: 25px; }



