.ProductItem {
  position: relative;
  overflow: hidden;
  width: 100%;
  padding: 5px;
  display: block;
  border-radius: 10px;
  border: 2px solid #f2f2f7;
  cursor: pointer; }
  .ProductItem:hover {
    z-index: 10;
    -webkit-box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.1);
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.1); }

.ProductItem_image {
  position: relative;
  width: 100%;
  height: 0;
  padding-top: 100%;
  background-color: white; }
  .ProductItem_image img {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    width: 100%; }

.ProductItem_content {
  text-align: center;
  padding: 10px; }

.ProductItem_name {
  font-weight: normal;
  color: #5a585e; }

.ProductItem_color {
  color: rgba(90, 88, 94, 0.3);
  font-size: 0.8em;
  margin-bottom: 5px; }

.ProductItem_price {
  color: #6079f2; }



