.TextInput {
  border: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  border-radius: 5px;
  padding: 12px 20px 13px;
  -webkit-box-shadow: 0 2px 5px rgba(90, 88, 94, 0.3);
  box-shadow: 0 2px 5px rgba(90, 88, 94, 0.3); }
  .TextInput:valid {
    color: #5a585e; }
  .TextInput:invalid {
    color: #9e2146; }



