.Button {
  border: none;
  outline: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: none; }
  .Button:not([disabled]) {
    cursor: pointer; }
  .Button[disabled] {
    opacity: 0.5; }

.Button-primary {
  color: white;
  padding: 10px 20px;
  border-radius: 50px;
  font-weight: 700;
  background-color: #6079f2;
  -webkit-transition: all 200ms ease;
  -o-transition: all 200ms ease;
  transition: all 200ms ease; }
  .Button-primary:hover {
    -webkit-transform: translateY(-3px);
    -ms-transform: translateY(-3px);
    transform: translateY(-3px); }

.Button-inverted {
  padding: 10px 20px;
  border-radius: 50px;
  font-weight: 700;
  background-color: white;
  color: #6079f2 !important;
  -webkit-transition: all 200ms ease;
  -o-transition: all 200ms ease;
  transition: all 200ms ease; }
  .Button-inverted:hover {
    -webkit-transform: translateY(-3px);
    -ms-transform: translateY(-3px);
    transform: translateY(-3px); }

.Button-secondary {
  padding: 5px 0;
  color: #6079f2; }

.Button-submit {
  display: block;
  padding: 12px 20px;
  border-style: none;
  border-radius: 5px;
  background-color: #6079f2;
  -webkit-box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.15);
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.15);
  font-weight: 500;
  text-align: center;
  color: #fff;
  cursor: pointer;
  outline: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  -webkit-transition: 200ms ease;
  -o-transition: 200ms ease;
  transition: 200ms ease; }
  .Button-submit:hover {
    -webkit-transform: translateY(-3px);
    -ms-transform: translateY(-3px);
    transform: translateY(-3px);
    -webkit-box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.15);
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.15); }

.Button-text {
  color: #6079f2; }



