.Storefront {
  display: -ms-flexbox;
  display: flex; }

.Storefront_form {
  -ms-flex-positive: 1;
  flex-grow: 1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
  flex-direction: column; }
  .Storefront_form h5 {
    margin: 20px 0 10px; }
  .Storefront_form label:first-child h5 {
    margin-top: 0; }
  .Storefront_form .TextArea,
  .Storefront_form .TextInput {
    width: 100%; }
  .Storefront_form .ImageUploader {
    width: 150px;
    height: 150px; }
  .Storefront_form .Button {
    margin-top: 30px; }

.Storefront_preview {
